@include laptop-landscape-large {
/* ==========================================================================
    #404 Page responsive - begin
========================================================================== */
    .mkdf-404-page .mkdf-page-not-found {
        padding: 0 15%;

        .mkdf-404-title-image {
            padding: 3% 0 0;
        }

        .mkdf-404-subtitle {
            padding: 0 9%;
        }

        .mkdf-404-text {
            padding: 0 18%;
        }
    }

/* ==========================================================================
    #404 Page responsive - end
========================================================================== */
}

@include laptop-landscape-medium {

/* ==========================================================================
    #404 Page responsive - begin
========================================================================== */

    .mkdf-404-page {

        .mkdf-content {
            height: 100%;

            .mkdf-page-not-found {
                padding: 7.5% 11% 7%;

                .mkdf-404-subtitle {
                    padding: 0 9%;
                }

                .mkdf-404-text {
                    padding: 0 15%;
                }
            }
        }
    }
/* ==========================================================================
    #404 Page responsive - end
========================================================================== */


}

