@include ipad-portrait {

    /* ==========================================================================
       #Custom Contact Forms responsive - begin
       ========================================================================== */
    .mkdf-subscribe {
        .mkdf-cf-name-column,
        .mkdf-cf-email-column,
        .mkdf-cf-website-column {

            &:before {
                left: 5%;
            }
        }
    }
    .mkdf-newsletter-form {

        &:before {
            left: 4.6%;
        }
    }

    .mkdf-404-page {

        .mkdf-content .mkdf-page-not-found {
            padding: 9.5% 10% 6%;

            .mkdf-404-text {
                padding: 0 11%;
            }
        }
    }

    /* ==========================================================================
       #Custom Contact Forms responsive - end
       ========================================================================== */
}

@include phone-portrait {

    /* ==========================================================================
       #Custom Contact Forms responsive - begin
       ========================================================================== */
    .mkdf-subscribe {
        .mkdf-cf-name-column,
        .mkdf-cf-email-column,
        .mkdf-cf-website-column {

            &:before {
                left: 10%;
            }
        }
    }

    .mkdf-newsletter-form {

        &:before {
            top: 13.5%;
            left: 7.6%;
        }

        input[type=email] {
            padding: 19px 18px 20px 66px;
        }

        input[type=submit] {
            top: inherit;
            right: 14.5%;
        }
    }

    .mkdf-404-page {

        .mkdf-content .mkdf-page-not-found {
            padding: 15.5% 5% 8%;

            .mkdf-404-text {
                padding: 0 3%;
            }
        }
    }

    /* ==========================================================================
       #Custom Contact Forms responsive - end
       ========================================================================== */
}